import React from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Switch } from "react-router-dom";
import Home from "./pages/Home.js";
import About from "./pages/About.js";
import Contact from "./pages/Contact.js";
import Projects from "./pages/Projects.js";
import * as serviceWorker from "./serviceWorker";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap/dist/js/bootstrap.bundle.min";

// Used to track website load time. Should be removed in future.
let n = Date.now();

// Which component to insert, where to insert it.
/*
ReactDOM.render(<Navigation />, document.getElementById("nav"));
ReactDOM.render(<Header />, document.getElementById("header"));
ReactDOM.render(<PortfolioSub />, document.getElementById("portfolio_sub"));
ReactDOM.render(<Portfolio />, document.getElementById("portfolio"));
ReactDOM.render(<Footer />, document.getElementById("footer"));
*/
//ReactDOM.render(<Slider />, document.getElementById("root"));

// Routing defines which component to load based on the URL. At the bottom it is defined in which div in the index.html file the components functionality should be added.
const routing = (
  // The HashRouter is required for the page switch to work on GitHub Pages. However it is better to use a BrowserRouter if page is not hosted on GitHub.
  // All links when using HashRouter need /#/ in front of them when accessing them in a browser.
  <HashRouter>
    <div>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/about" component={About} />
        <Route exact path="/contact" component={Contact} />
        <Route exact path="/projects" component={Projects} />
        <Route exact path="/portfolio" component={Contact} />
      </Switch>
    </div>
  </HashRouter>
);

ReactDOM.render(routing, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

// Used to track website load time. Should be removed in future. Result precision unknown.
let n2 = Date.now();
console.log("Website loaded in: ", n2 - n, " ms");
