import React, { Component } from "react";
import "./about.css";

class About extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="container-fluid">
          <div className="row justify-content-center bg-dark">
            <div className="col-0 text-white">
              <h1>About Me</h1>
            </div>
          </div>
          <div className="row justify-content-center bg-dark">
            <div className="col-0 text-white">
              <img src="https://i.imgur.com/pvNYpOY.jpg" className="img-fluid img-thumbnail img-circle" width="200px" alt="Image of me" />
            </div>
          </div>
          <div className="row justify-content-center bg-dark">
            <div className="col-0 text-white my-col-about">
              <p>
                My name is Jonas Støve Rasmussen and I am 22 years old. I am currently studying at <a href="https://www.sdu.dk/en">University of Southern Denmark (SDU)</a> to become a Software Engineer. My goal is to get my Master's Degree in the summer 2021.
              </p>
              <p>I enjoy a good challenge and through my education I have gained a lot of experience in problem solving, group work, programming and software architecture to name a few. This gives me a good basis for contributing with knowledge that I have learnt already but also to quickly adjust to new technologies and their appliance.</p>
              <p>All in all I would say I am a person with a good drive and I work well together with others. I am good at listening but at the same time not afraid to contribute to discussions with my opinion and knowledge which I have experienced provide good results. </p>
              <p>
                <h5>
                  <b>Core Compentencies</b>
                </h5>
              </p>
              <ul>
                <li class="mr-4 float-left">Ambitious</li>
                <li class="mr-4 float-left">Analytical</li>
                <li class="mr-4 float-left">Positive</li>
                <li class="mr-4 float-left">Teamplayer</li>
                <li class="mr-4 float-left">Structured</li>
                <li class="mr-4 float-left">Comitted</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
